@import "../../../global.scss";
.menu{
    width: 300px;
    height: 100vh;
    background-color: $menuClickColor;
    position:fixed;
    top:0;
    right:-300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    &.active{
        right:0;
    }
    ul{
        margin:0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 200;
        color: white;
        width: 70%;
        li{
            margin-bottom: 30px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
            &:hover{
                font-weight: 500;
            }
        }
        
    }
}